<script>
  import { fly } from "svelte/transition";
  import { createEventDispatcher } from "svelte";
  import answersStore from "./stores/answers-store";
  import Answer from "./Answer.svelte";
  // bubble up an event to the parent component
  const dispatch = createEventDispatcher();
  export let question;

  export let game_id;
  export let user_code;

  let answer_text, question_title;

  let can_say_ready = true;
  let can_vote = true;
  let waiting_for_answer = false;
  let can_answer = true;
  let is_correct = false;
  $: if (question) {
    if (question.user) {
      question_title = question.text.replace(
        "@@user@@",
        question.user.nickname
      );

      if (question.user.code == user_code) {
        is_correct = true;
      }
    } else {
      question_title = question.text;
    }
  }

  function handleKeydown() {}

  function handleSubmit() {
    if (can_answer) {
      can_answer = false;
      const answer = {
        user_code: user_code,
        game_id: game_id,
        question_id: question.id,
        text: answer_text,
        correct: is_correct
      };
      answersStore
        .createAnswer(answer)
        .then(new_answer => {
          // dispatch("answered", "answered");
          waiting_for_answer = true;
          dispatch("requestpolling", "start");
          return true;
        })
        .catch(error => {
          console.error(error);
        });
    }
  }

  function answerVoted(e) {
    if (e.detail) {
      if (e.detail === "voted") {
        can_vote = false;
        dispatch("requestpolling", "start");
      }
    }
  }

  function sayReady() {
    if (can_say_ready) {
      can_say_ready = false;
      const answer = {
        user_code: user_code,
        game_id: game_id,
        question_id: question.id,
        finished: 1
      };
      answersStore.markAnswerAsFinished(answer).then(newanswer => {
        dispatch("requestpolling", "start");
        return true;
      });
    }
  }
</script>

<style>

</style>

<svelte:window on:keydown={handleKeydown} />

<div class="box" in:fly={{ delay: 500, duration: 500, y: 20, opacity: 0 }}>
  <h2>
    {@html question_title}
  </h2>

  {#if question.finishedVoting}
    <ul>
      {#each question.answers as answer, i}
        <li>
          <Answer
            delay={0}
            show_results={true}
            {answer}
            {user_code}
            {question}
            can_vote={false} />
        </li>
      {/each}
    </ul>

    {#if can_say_ready}
      <p>
        <a href="#ready" on:click|preventDefault={sayReady} class="button">
          Ready
        </a>
      </p>
    {/if}
  {:else if question.answered}
    {#if question.answers}
      <ul>
        {#each question.answers as answer, i}
          <li>
            <Answer
              {answer}
              {question}
              {can_vote}
              {user_code}
              show_results={false}
              delay={i * 1000}
              on:voted={answerVoted} />
          </li>
        {/each}
      </ul>

      {#if !can_vote}
        <div class="spinner" />
      {/if}
    {/if}
  {:else if can_answer}
    <form on:submit|preventDefault={handleSubmit}>
      <input placeholder="Your answer" type="text" bind:value={answer_text} />
    </form>
  {:else if waiting_for_answer}
    <div class="spinner" />
  {/if}

</div>
