import { writable, get } from "svelte/store";
import constants from "../shared/constants";


// store a user object from the api if logged in
const store = writable(null);


export default {
    subscribe: store.subscribe,
    storeUser: (user) => {
        store.set(user);
        const obj = { nickname: user.nickname, code: user.code };
        localStorage.setItem(constants.token_user, JSON.stringify(obj))
    },
    getUser: () => {
        const token = localStorage.getItem(constants.token_user);
        if (token) {
            const user = JSON.parse(token);
            store.set(user);
            return user;
        } else {
            return null;
        }
    },
    logout: async () => {
        store.set(null);
        localStorage.removeItem(constants.token_user);
        return await true;
    },
    authenticated: () => get(store) !== null,
    createUser: async nickname => {
        const data = { nickname };
        const authParams = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=users`, authParams);
        if (response.ok) {
            const new_user = await response.json();

            return new_user;
        } else {
            const error = await response.json();
            throw error;
        }
    }
};
