<script>
  import { fly } from "svelte/transition";

  import gamesStore from "./stores/games-store";
  import { createEventDispatcher } from "svelte";
  import Question from "./Question.svelte";

  // bubble up an event to the parent component
  const dispatch = createEventDispatcher();

  export let game;
  export let user;
  $: user_code = user.code;
  $: game_id = game.id;

  let can_say_ready = true;
  let show_results = false;

  $: if (game) {
    // SHOW THE FIRST UNANSWERED QUESTION

    // add a user to each question to ask a personalised question
    addUserToQuestion();
    addAnswersToQuestions();

    if (game.finished) {
      stopPollingServer();
    } else if (game.started) {
      showFirstUnansweredQuestion();
    }
  }

  function showFirstUnansweredQuestion() {
    const unanswered = game.game_questions.filter(gaqu => !gaqu.finished);

    if (unanswered.length > 0) {
      const gaqu = unanswered[0];
      const question = game.questions.find(q => q.id === gaqu.question_id);
      if (question) {
        question.active = true;

        question.finishedVoting = gaqu.voted;
        if (question.finishedVoting) {
          //  stopPollingServer();
        }
      }
    } else {
      // no unansswered questions
      // show final results
      show_results = true;
      stopPollingServer();
    }
  }

  function stopPollingServer() {
    dispatch("pollchange", "stop");
  }
  function startPollingServer() {
    dispatch("pollchange", "start");
  }

  function requestPolling(e) {
    if (e.detail) {
      if (e.detail === "start") {
        console.log("staring agin");
        startPollingServer();
      }
    }
  }

  function questionAnswered(e) {
    if (e.detail) {
      if (e.detail === "answered") {
      }
    }
  }

  function addAnswersToQuestions() {
    if (game.answers) {
      game.questions.forEach((question, i) => {
        question.answers = game.answers.filter(
          a => a.question_id === question.id
        );
      });
    }
  }
  function addUserToQuestion() {
    game.questions.forEach((question, i) => {
      const index = (i + game.id * 161) % game.users.length;
      question.user = game.users[index];
    });
  }

  function sorted(usrs) {
    return usrs.sort((a, b) => {
      if (a.score < b.score) {
        return 1;
      }
      return -1;
    });
  }

  function endGame() {
    dispatch("gameended", true);
  }

  function sayReady() {
    if (game.users.length > 1) {
      if (can_say_ready) {
        if (game.user_games) {
          let user_game = game.user_games.find(ug => ug.code === user.code);
          if (user_game && user_game.ready !== true) {
            user_game.ready = true;
            gamesStore.updateUserGame(user_game).then(new_user_game => {
              if (new_user_game) {
                can_say_ready = false;
                return true;
              }
            });
          }
        }
      }
    }
  }
</script>

<div>

  {#if show_results}
    <div class="box">
      <h2>Results</h2>
      <ul class="results">
        {#each sorted(game.users) as user}
          <li>
            {user.nickname} -
            <strong>{user.score} points</strong>
          </li>
        {/each}
      </ul>
      <p>
        <a class="button" on:click|preventDefault={endGame} href="#reset">
          End game
        </a>
      </p>
    </div>
  {:else if game.started}
    {#each game.questions as question}
      {#if question.active}
        <Question
          {question}
          {game_id}
          {user_code}
          on:requestpolling={requestPolling}
          on:answered={questionAnswered} />
      {/if}
    {/each}
  {:else}
    <div class="box">
      <h2>Waiting for players</h2>

      <ul class="player_list">
        {#each game.users as user}
          <li>
            {user.nickname}
            {#if user.ready}(READY){/if}
          </li>
        {/each}
      </ul>

      {#if can_say_ready && game.users.length > 1}
        <p>
          <a class="button" on:click|preventDefault={sayReady} href="#ready">
            Ready
          </a>
        </p>
      {/if}

      <br />
      <br />
      <div class="form_field">
        <label for="code">Invite others with this code:</label>
        <input type="text" id="code" name="code" disabled value={game.code} />
      </div>
    </div>
  {/if}

</div>
