<script>
  import { onDestroy, onMount } from "svelte";
  import constants from "./shared/constants";
  import authStore from "./stores/auth-store";
  import gamesStore from "./stores/games-store";
  import Game from "./Game.svelte";

  let current_user, unsubscribeUser, nickname;
  let current_game, unsubscribeGame;
  let shouldPoll = true;
  let joiningGame = false;

  let game_code;

  //  schedules a callback to run as soon as the component has been mounted to the DOM.
  onMount(() => {
    getUserFromStorage();
  });

  unsubscribeUser = authStore.subscribe(user => {
    if (user) {
      current_user = user;
      getGame();
      pollGame();
    }
  });

  unsubscribeGame = gamesStore.subscribe(game => {
    if (game) {
      current_game = game;
      console.log(game);
      processGame(game);
    }
  });

  function processGame(game) {
    game.answers.forEach(a => {
      a.user = game.users.find(u => u.id === a.user_id);
    });
    game.questions.forEach(q => {
      q.answers = game.answers.filter(a => a.question_id === q.id);
    });

    game.users.forEach(u => {
      u.answers = game.answers.filter(a => a.user_id === u.id);
      u.votes = game.votes.filter(v => v.user_id === u.id);
      let score = 0;
      u.answers.forEach(a => {
        score += a.score;
      });
      u.votes.forEach(v => {
        score += v.score;
      });

      u.score = score;
    });
  }

  function pollGame() {
    setInterval(() => {
      if (shouldPoll) {
        getGame();
      }
    }, constants.polling_time);
  }

  function getUserFromStorage() {
    authStore.getUser(user => {
      if (user) {
        return true;
      }
    });
  }

  function getGame() {
    const game_id = gamesStore.gameTokenFromStorage();
    if (game_id) {
      gamesStore.getGame(game_id).then(game => {
        if (game) {
          return true;
        }
      });
    }
  }

  function joinGame() {
    joiningGame = true;
    gamesStore
      .joinGame(current_user, game_code)
      .then(game => {
        return true;
      })
      .catch(error => {
        console.error(error);
        joiningGame = false;
      });
  }

  function createUser() {
    authStore
      .createUser(nickname)
      .then(new_user => {
        authStore.storeUser(new_user);
      })
      .catch(error => {
        console.error(error);
      });
  }

  function createGame() {
    gamesStore
      .createGame(current_user)
      .then(new_game => {
        return true;
      })
      .catch(error => {
        console.error(error);
      });
  }

  function changePolling(e) {
    if (e.detail) {
      if (e.detail === "stop") {
        shouldPoll = false;
      } else {
        shouldPoll = true;
      }
      shouldPoll = shouldPoll;
    }
  }

  function endGame() {
    gamesStore.deleteGameTokenFromStorage();
    current_game = null;
    joiningGame = false;
    shouldPoll = true;
    game_code = null;
  }

  //  when component is destroy, unsubscribe from any subscriptions to prevent memory leaks
  onDestroy(() => {
    if (unsubscribeUser) {
      unsubscribeUser();
    }
  });
</script>

<header>

  <h1>{constants.app_name}</h1>

</header>

<main>

  <div class="container">

    {#if current_user}
      {#if current_game}
        <Game
          game={current_game}
          user={current_user}
          on:pollchange={changePolling}
          on:gameended={endGame} />

        <p class="goback">
          <a href="#goback" on:click|preventDefault={endGame}>Go Back</a>
        </p>
      {:else if joiningGame}
        <div class="spinner" />
      {:else}
        <div class="box">
          <h2>Welcome {current_user.nickname}</h2>

          <form action="#" on:submit|preventDefault={joinGame}>
            <div class="form_field">
              <input
                type="text"
                name="game_code"
                id="game_code"
                placeholder="enter a code to join a game "
                bind:value={game_code} />
            </div>
            <div class="form_field">
              <button>Join Game</button>
            </div>
          </form>
          <br />
          <p>or</p>
          <p>
            <a
              on:click|preventDefault={createGame}
              href="#new_game"
              class="button">
              Start a new Game
            </a>
          </p>
        </div>
      {/if}
    {:else}
      <div class="box">
        <form on:submit|preventDefault={createUser}>
          <div class="form_field">
            <label for="nickname">username</label>
            <input
              type="text"
              name="nickname"
              id="nickname"
              bind:value={nickname} />
          </div>

          <div class="form_field">
            <button>Register</button>
          </div>

        </form>
      </div>
    {/if}

  </div>

  {#if shouldPoll}
    <div class="polling">
      <div class="spinner " />
    </div>
  {/if}

</main>
