import { writable, get } from "svelte/store";
import constants from "../shared/constants";


// store a answer object from the api if logged in
const store = writable(null);


export default {
    subscribe: store.subscribe,

    createAnswer: async (answer) => {
        const data = answer;
        const authParams = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=answers`, authParams);
        if (response.ok) {
            const new_answer = await response.json();
            return new_answer;
        } else {
            const error = await response.json();
            throw error;
        }
    },
    createVote: async (vote) => {
        const data = vote;
        const authParams = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=votes`, authParams);
        if (response.ok) {
            const new_vote = await response.json();
            return new_vote;
        } else {
            const error = await response.json();
            throw error;
        }
    },
    markAnswerAsFinished: async (answer) => {
        const data = answer;
        const authParams = {
            method: 'PATCH',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=answers&id=${answer.id}`, authParams);
        if (response.ok) {
            const new_answer = await response.json();
            return new_answer;
        } else {
            const error = await response.json();
            throw error;
        }
    }

};
