import { writable, get } from "svelte/store";
import constants from "../shared/constants";


// store a game object from the api if logged in
const store = writable(null);


export default {
    subscribe: store.subscribe,
    gameTokenFromStorage: () => {
        return localStorage.getItem(constants.token_game);
    },
    deleteGameTokenFromStorage: () => {
        localStorage.removeItem(constants.token_game);
        store.set(null);
    },
    getGame: async game_id => {
        const authParams = {
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=games&id=${game_id}`, authParams);
        if (response.ok) {
            const game = await response.json();
            localStorage.setItem(constants.token_game, game.id)
            store.set(game);
            return game;
        } else {
            const error = await response.json();
            throw error;
        }
    },
    joinGame: async (user, game_code) => {
        const data = { user_code: user.code, game_code: game_code.replace(' ', '') };
        console.log(data);
        const authParams = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=user_games`, authParams);
        if (response.ok) {
            const new_game = await response.json();
            localStorage.setItem(constants.token_game, new_game.id)
            return new_game;
        } else {
            const error = await response.json();
            throw error;
        }
    },
    createGame: async (user) => {
        const data = { user_code: user.code };
        const authParams = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=games`, authParams);
        if (response.ok) {
            const new_game = await response.json();
            store.set(new_game);
            localStorage.setItem(constants.token_game, new_game.id)
            return new_game;
        } else {
            const error = await response.json();
            throw error;
        }
    },
    updateUserGame: async (user_game) => {
        const data = user_game;
        const authParams = {
            method: 'PATCH',
            body: JSON.stringify(data),
            headers: constants.no_auth_headers
        }
        let response = await fetch(`${constants.api_url}/?route=user_games&id=${user_game.id}`, authParams);
        if (response.ok) {
            const new_user_game = await response.json();
            return new_user_game;
        } else {
            const error = await response.json();
            throw error;
        }
    }
};
